import React from "react";
import { GoClock } from "react-icons/go";
import { GiCampCookingPot, GiKnifeFork } from "react-icons/gi";
import { useEffect, useState } from "react";
import { Margin } from "../components/Styles";
import styled from "styled-components";
import { useParams } from "react-router-dom";
import Recommendations from "../components/Recommendations";
import CommentBox from "../components/Comment";

export default function Recipe() {
  const [activeTab, setActiveTab] = useState("ingredients");
  let params = useParams();
  const [details, setDetails] = useState({});

  const getRecipeDetails = async () => {
    const api_key = "a81060e9446f4c0da0648da3da727fd8";
    const data = await fetch(
      `https://api.spoonacular.com/recipes/${params.name}/information?apiKey=${api_key}`
    );
    const recipeDetailData = await data.json();
    setDetails(recipeDetailData);
  };

  useEffect(() => {
    getRecipeDetails();
  }, [params.name]);
  console.log(details);

  return (
    <>
      <Recipestyle>
        <div className="icons">
          <div className="details">
            <GiKnifeFork />
            <p>
              Servings: <br />
              {details.servings}
            </p>
          </div>
          <div className="details">
            <GoClock />
            <p>
              Prep. Time: <br />
              {details.preparationMinutes} MIN
            </p>
          </div>
          <div className="details">
            <GiCampCookingPot />
            <p>
              Cooking Time:
              <br />
              {details.cookingMinutes} MIN
            </p>
          </div>
          <div className="details">
            <GiCampCookingPot />
            <p>
              Ready In:
              <br />
              {details.readyInMinutes} MIN
            </p>
          </div>
        </div>

        <div>
          <img src={details.image} />
        </div>
        <Margin>
          <h2>{details.title}</h2>

          <MobileOnly>
            <div className="buttons">
              <Button
                className={activeTab === "ingredients" ? "active" : ""}
                onClick={() => setActiveTab("ingredients")}
              >
                Ingredients
              </Button>
              <div className="vl1"></div>
              <Button
                className={activeTab === "instructions" ? "active" : ""}
                onClick={() => setActiveTab("instructions")}
              >
                {" "}
                Instruction
              </Button>
            </div>
            {activeTab === "ingredients" && (
              <div className="ingredient-mobile">
                <ol>
                  {details.extendedIngredients?.map((x) => (
                    <li key={x.id}> {x.original} </li>
                  ))}
                </ol>
              </div>
            )}
            {activeTab === "instructions" && (
              <div className="instruction-mobile">
                <div
                  dangerouslySetInnerHTML={{ __html: details.instructions }}
                  className="display-linebreak"
                ></div>
              </div>
            )}
          </MobileOnly>

          <DesktopOnly>
            <div className="flex">
              <div className="ingredient">
                <h3>INGREDIENT</h3>
                <ol>
                  {details.extendedIngredients?.map((x) => (
                    <li key={x.id}> {x.original} </li>
                  ))}
                </ol>
              </div>

              <div className="instruction">
                <h3>INSTRUCTION</h3>
                <li
                  dangerouslySetInnerHTML={{ __html: details.instructions }}
                  className="display-linebreak"
                ></li>
              </div>
            </div>
          </DesktopOnly>
          <hr />
        </Margin>
      </Recipestyle>
      <Margin>
        <CommentBox />
        <hr />
        <Recommendations />
      </Margin>
    </>
  );
}

const Recipestyle = styled.div`
  margin-top: 6rem;

  .vl1 {
    border-left: 1.5px solid grey;
    height: 2rem;
  }
  .display-linebreak {
    white-space: pre-line;
    font-size: 0.9rem;
    line-height: 2.5rem;
    display: inline;
  }

  div img {
    height: 40vh;
    @media (max-width: 576px) {
      height: 40vh;
      width: 100%;
    }
  }
  img {
    width: 100%;

    margin: auto;
    display: block;
    height: 100%;
  }

  .icons {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    margin-bottom: 1.2rem;
  }

  h2 {
    width: 60%;
    text-align: center;
    margin: auto;
    margin-top: 1rem;
    @media (max-width: 768px) {
      width: 80%;
    }
  }
  .vl {
    border-left: 1.5px solid grey;
    height: 15rem;
    margin: 1rem 2rem;
  }

  .active {
    border-bottom: solid blue;
  }
  ol {
    counter-reset: list -0; /*start from 0,1,2,3... name List - customize*/
    margin: 0;
  }
  ol li {
    list-style-type: none;
    position: relative;
    margin: 25px 0;

    padding-left: 58px;
    font-size: 0.9rem;
    line-height: 1.5rem;
  }
  ol li:before {
    content: counter(list) "";
    counter-increment: list;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 37px;
    background: url(data:image/png;base64,);
    font-size: 18px;
    line-height: 30px;
    font-weight: bold;
    color: #fff;
    text-align: center;
  }
  p {
    font-weight: bold;
  }
`;

const Button = styled.button`
  width: 100%;
  color: #313131;
  background: white;
  border: none 2px;
  border-left: 2px;
  font-weight: 600;
  display: flex;
  font-size: 1.5rem;
  display: flex;
  text-align: center;
  justify-content: center;
`;
const MobileOnly = styled.div`
  display: none;
  @media (max-width: 768px) {
    display: block;
  }
  .buttons {
    display: flex;
    text-align: center;
    justify-content: center;
    margin: 2rem 0;
    @media (max-width: 576px) {
      font-size: 9px;
    }
  }
`;

const DesktopOnly = styled.div`
  display: block;
  @media (max-width: 768px) {
    display: none;
  }
  .instruction {
    width: 50vw;
  }
  .ingredient {
    width: 50vw;
    padding-right: 2rem;
  }
  .flex {
    display: flex;
    margin-top: 3rem;
  }
  h3 {
    font-size: 1.5rem;
    font-weight: light;
    text-align: center;
    font-family: "Paytone One", sans-serif;
  }
`;
